import Nav from 'react-bootstrap/Nav';
import React from 'react';
import * as Scroll from 'react-scroll';
/*
Override the active proper
*/
class CustomLink extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
      return (
        <Nav.Link {...this.props}>
          {this.props.children}
        </Nav.Link>
      );
    }
}

export default Scroll.ScrollLink(CustomLink);