import React, { Component } from 'react';
import { ThemeConsumer } from 'react-bootstrap/esm/ThemeProvider';
import Card from 'react-bootstrap/Card';
import CustomButton from './CustomButton';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';

const styles = {
    center: {display:"flex",
            justifyContent:"center",
            alignItems:"center"},
  }
const INIT_STATE = {
}

const scrollWithOffset = (el, offset) => {
    const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
    const yOffset = -80; 
    window.scrollTo({ top: yCoordinate + yOffset, behavior: 'smooth' }); 
}  

class PricingCard extends Component {
  constructor(props){
    super(props);
    this.state = {...INIT_STATE}
  }
  render() {
    return (
        <Card style={{ width: '22rem', margin:30,marginTop:10,marginBottom:10 }}>
            <div style={{...styles.center, backgroundColor:"lightgrey"}}>
            <h2 style={{margin:0,fontWeight:300}}>{this.props.title}</h2>
            </div>
            <Card.Body>
                <Card.Title style={styles.center}>{this.props.price}</Card.Title>
                <Card.Text>
                {this.props.desc}
                </Card.Text>
                
            </Card.Body>
            <div style={{display: "flex", justifyContent: "center", marginBottom:10}}>
                <CustomButton variant="primary" activeClass="active" className="btn" type="submit" value="Test 2" to="contact" spy={true} smooth={true} offset={-this.props.offset} duration={500} >
                Contact
                </CustomButton>
            </div>
            </Card> 
    );
  }
}

export default PricingCard;