import Button from 'react-bootstrap/Button';
import React from 'react';
import * as Scroll from 'react-scroll';
/*
Override the active proper
*/
class CustomButton extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
      return (
        <Button {...this.props}>
          {this.props.children}
        </Button>
      );
    }
}

export default Scroll.ScrollLink(CustomButton);